/* src/components/Navbar.css */

.navbar {
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    background: linear-gradient(135deg, #311b92, #afa1ed); /* Dark purple gradient */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  }
  

  .navbar-left {
    width: 767px;
    color: #ffffff;
  }

  .logo {
    color: #ffffff;
    font-size: 24px;
    font-weight: bold;
  }
  
  .nav-menu {
    flex: 1;
    margin-left: 50px;
    background: #311b92;
    border-bottom: none;
  }
  
  .nav-menu .ant-menu-item {
    color: #d1c4e9; /* Light purple for menu items */
    font-size: 16px;
  }
  
  .nav-menu .ant-menu-item:hover,
  .nav-menu .ant-menu-item-active,
  .nav-menu .ant-menu-item-selected {
    color: #ffffff; /* White text on hover */
    background: rgba(255, 255, 255, 0.1); /* Light purple background on hover */
  }
  
/* src/styles/Navbar.css */

.wallet-button {
    background: linear-gradient(to bottom, #512da8, #311b92) !important; /* Dark purple gradient */
    border: 2px solid #ffffff !important; /* White border around button */
    color: #ffffff !important;
    border-radius: 4px !important;
    padding: 0 20px !important;
    font-weight: bold !important;
    transition: all 0.3s ease !important; /* Smooth transition for hover effect */
  }
  
  .wallet-button:hover,
  .wallet-button:focus {
    background: #b39ddb !important; /* Darker gradient on hover */
    border-color: #b39ddb !important; /* Light purple border on hover */
    color: #ffffff !important; /* Ensure text color stays white */
    box-shadow: none !important; /* Remove any default Ant Design box shadow on hover */
  }
  
  .wallet-button:active {
    background: #280072 !important; /* Darkest gradient on click */
    border-color: #280072 !important; /* Slightly darker border on click */
    color: #ffffff !important;
  }
  
  